.sidebar {
  position: fixed;
  top: 0;
  max-width: 1200px;
  width: 100%;
  height: 0px;
  left: 50%;
  transform: translateX(-50%);
  &__logo {
    width: 130px;
    height: 165px;
    background-image: url('/img/logo/logo-bg.svg');
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.3s;
    &.green {
       background-image: url('/img/logo/logo-bg-green.svg'); }
    img {
      width: 80px;
      height: 90px; } }
  &__menu {
    display: flex;
    width: 200px;
    margin-top: 70px;
    ul {
      a {
        font-size: 12px;
        font-weight: 600;
        display: inline-block;
        letter-spacing: 0.9px;
        text-decoration: none;
        color: $black;
        text-transform: uppercase;
        line-height: 4;
        position: relative;
        z-index: 10;
        padding: 0 10px;
        overflow: hidden;
        &:hover:before {
          opacity: 1;
          transition: all 0.3s;
          transform: translateX(50%); }
        &:before {
          transform: translateX(-100%);
          content: '';
          width: 130%;
          right: 50%;
          background: #fff;
          height: 10px;
          display: inline-block;
          position: absolute;
          top: 23px;
          z-index: -1;
          transition: all 0.3s; } } } } }
